import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { createPlaylist } from '../utils/hooks';
import { TimeDisplay } from '../TimeDisplay';
import styles from './player.scss';
import { circleData, canvasWidth, mainRadius, pointsLine, pointsTriangle } from '../geometry.const';

const MAX_CHUNKS = 3;
const INITIAL_CHUNK = 1;


const pauseBar1X = 242;
const pauseBar1Y = 392;
const pauseBar2X = pauseBar1X + 10;

const playPoint1 = `240,${pauseBar1Y}`;
const playPoint2 = '240,418';
const playPoint3 = '262,405';


const nameByChapter = {
  1: 'Act I',
  2: 'Act II',
  3: 'Act III',
  4: 'Epilogue'
};

const Player = ({ speakers }) => {
  const [speakerIndex, setSpeakerIndex] = useState(/** @type number */ undefined);
  const [time, setTime] = useState(0);
  const [streams, setStreams] = useState(/** @key Object<Howl[]>*/ undefined);
  const [chapter, setChapter] = useState(1);
  const [isPlaying, setIsPlaying] = useState(false);
  const [textOpacity, setTextOpacity] = useState(0);
  const [displayName, setDisplayName] = useState('');


  useEffect(() => {
    if (!speakers[speakerIndex]) {
      setDisplayName('');
      return undefined;
    }
    setTextOpacity(0);
    const t = setTimeout(() => {
      setTextOpacity(1);
      setDisplayName(speakers[speakerIndex].title);
    }, 1000);

    return () => {
      clearTimeout(t);
    };
  }, [speakerIndex]);

  /**
   * stream management
   */
  useEffect(() => {
    const s = {};
    speakers.forEach((speaker, index) => {
      s[index] = createPlaylist(speaker.track, 3, speaker.ext, () => {
        setChapter((prevState) => {
          if (prevState === MAX_CHUNKS) {
            console.log('ended');
            setTime(0);
            return INITIAL_CHUNK;
          }
          console.log('next chunk');
          return prevState + 1;
        });
      });
    });

    setStreams(s);
  }, [speakers]);


  /**
   * preloading
   */
  useEffect(() => {
    if (streams === undefined) return;
    if (speakerIndex === undefined) {
      Object.keys(streams).forEach((key) => {
        streams[key][INITIAL_CHUNK].init();
      });
      return;
    }

    // load the next stream
    const nextChunk = chapter + 1;
    if (nextChunk <= MAX_CHUNKS) {
      Object.keys(streams).forEach((key) => {
        streams[key][nextChunk].init();
      });
    }
  }, [streams, chapter]);


  /**
   * Handle playing
   */
  useEffect(() => {
    if (streams === undefined || speakerIndex === undefined) return undefined;
    if (isPlaying) {
      streams[speakerIndex][chapter].stream.play();
      const t = setInterval(() => {
        setTime(streams[speakerIndex][chapter].stream.seek());
      }, 100);

      return () => {
        if (t) {
          clearInterval(t);
        }
      };
    } else {
      streams[speakerIndex][chapter].stream.pause();
    }
    return undefined;
  }, [isPlaying]);

  useEffect(() => {
    if (streams === undefined || speakerIndex === undefined) return undefined;
    if (isPlaying) {
      streams[speakerIndex][chapter].stream.seek(time);
      streams[speakerIndex][chapter].stream.play();
    } else {
      streams[speakerIndex][chapter].stream.pause();
    }

    return () => {
      streams[speakerIndex][chapter].stream.stop();
    };
  }, [speakerIndex, streams, chapter]);

  // return <TimeDisplay time={time} />;

  return (
    <>
      <svg width={canvasWidth} height={canvasWidth + 200} className={styles.logo}>
        <circle cx={canvasWidth / 2} cy={canvasWidth / 2} r={mainRadius} />
        <polyline points={pointsTriangle} />
        <polyline points={pointsLine} />
        {
          circleData.map(({ x, y, r, className }, index) => {
            return (
              <circle
                role="button"
                key={`${x},${y}`}
                tabIndex="0"
                cx={x}
                cy={y}
                r={r}
                // style={{ fill: speakerIndex === index ? 'red' : 'inherit' }}
                className={speakerIndex === index ? styles.selected : styles.circle}
                onClick={() => {
                  setSpeakerIndex(index);
                }}
                onKeyDown={(e) => {
                  // spacebar or return
                  if (e.keyCode === 32 || e.keyCode === 13) {
                    e.preventDefault();
                    setSpeakerIndex(index);
                  }
                }}
              />
            );
          })
        }
        <text x="50%" y="45%" style={{ opacity: textOpacity }}>
          {displayName}
        </text>
        <text x="50%" y="50%" style={{ opacity: textOpacity }}>
          {nameByChapter[chapter]}
        </text>
        <g
          className={styles.controls}
          role="button"
          tabIndex="0"
          onClick={() => {
            setIsPlaying((prev) => !prev);
          }}
          style={{ opacity: speakerIndex === undefined ? 0 : 1 }}
        >
          <rect x={230} y="55%" width={40} height={40} rx={5} ry={5} />
          <g className={styles.controlIcon}>
            {
              isPlaying
                ? (
                  <g>
                    <rect x={pauseBar1X} y={pauseBar1Y} width={5} height={25} rx={2} ry={2} />
                    <rect x={pauseBar2X} y={pauseBar1Y} width={5} height={25} rx={2} ry={2} />
                  </g>
                )
                : <polygon points={`${playPoint1} ${playPoint2} ${playPoint3} ${playPoint1}`} />

            }
          </g>
        </g>
        Sorry, your browser does not support inline SVG.
      </svg>
      {
        __DEVELOPMENT__ ? <TimeDisplay time={time} /> : undefined
      }
    </>
  );
};

Player.propTypes = {
  speakers: PropTypes.arrayOf(PropTypes.shape({
    title: PropTypes.string,
    track: PropTypes.string
  }))
};

Player.defaultProps = {
  speakers: []
};

export {
  Player
};
