import React, { useEffect, useState } from 'react';
import styles from './app.scss';
import { Player } from './player/Player';
import {
  circleData,
  canvasWidth,
  pointsTriangle,
  pointsLine,
  mainRadius
} from './geometry.const';


const speakers = [
  {
    title: 'Richard Brake',
    // track: 'speaker1'
    track: 'RichardBrake'
  },
  {
    title: 'Jeff Steitzer',
    track: 'JeffSteitzer'
  },
  {
    title: 'Jen Taylor',
    track: 'JenTaylor'
  },
  {
    title: 'Tommie Earl Jenkins',
    track: 'TommieEarlJenkins'
  }
  // {
  //   title: 'Ensemble',
  //   track: 'ensemble'
  // }
];

const introCircleData = [
  {
    ...circleData[0],
    className: styles.speaker1
  },
  {
    ...circleData[1],
    className: styles.speaker2
  },
  {
    ...circleData[2],
    className: styles.speaker3
  },
  {
    ...circleData[3],
    className: styles.speaker4
  },
  {
    ...circleData[4],
    className: styles.speaker5
  }
];


const App = () => {
  const [displayIntro, setDisplayIntro] = useState(true);

  useEffect(() => {
    let t = setTimeout(() => {
      setDisplayIntro(false);
      t = undefined;
    }, 14000);
    return () => {
      if (t) {
        clearTimeout(t);
      }
    };
  }, []);

  return (
    <div className={styles.main}>
      {
        displayIntro
          ? (
            <svg width={canvasWidth} height={canvasWidth + 200} className={styles.logo}>
              <circle cx={canvasWidth / 2} cy={canvasWidth / 2} r={mainRadius} />
              <polyline points={pointsTriangle} />
              <polyline points={pointsLine} />
              {
                introCircleData.map(({ x, y, r, className }, index) => {
                  return (
                    <circle key={`${x},${y}`} cx={x} cy={y} r={r} className={className} />
                  );
                })
              }

              <text x="50%" y="45%">
                REALMDRIFTER
              </text>
              Sorry, your browser does not support inline SVG.
            </svg>
          )
          : undefined
      }
      <Player speakers={speakers} />
    </div>
  );
};

App.propTypes = {};
App.defaultProps = {};

export default App;
